.nav {
  display: flex;
  width: 300px;
  justify-content: space-between;
  border: 1px solid black;

  a {
    color: #fff;
    position: relative;
    text-decoration: none;

    &:hover {
      color: #fa5b0f
    }

    &::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 2px;
      border-radius: 4px;
      background-color: #fa5b0f;
      bottom: 0;
      left: 0;
      transform-origin: right;
      transform: scaleX(0);
      transition: transform .3s ease-in-out;
    }

    &:hover::before {
      transform-origin: left;
      transform: scaleX(1);
    }
  }
}




